.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}




        #protectedContent {
            display: none; /* Oculta el contenido protegido inicialmente */
            margin-top: 20px;
            padding: 10px;
            border: 1px solid #ccc;
        }
    
        body {
            font-family: Arial, sans-serif;
            margin: 0;
            padding: 0;
        }
        .container {
            display: flex;
            flex-wrap: wrap; /* Permite que las columnas se ajusten en pantallas pequeñas */
            margin: 20px;
        }
        .column {
            flex: 1; /* Cada columna ocupará el mismo espacio */
            padding: 10px;
            box-sizing: border-box; /* Incluye padding en el ancho total */
            min-width: 200px; /* Ancho mínimo para cada columna */
        }
        .column1 {
            background-color: #f2f2f2; /* Color de fondo para la columna 1 */
        }
        .column2 {
            background-color: #e6e6e6; /* Color de fondo para la columna 2 */
        }
        .column3 {
            background-color: #d9d9d9; /* Color de fondo para la columna 3 */
        }
    